import {
  search,
  searchsave,
} from '../../utils/service';
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import {
  locale,
  loadMessages
} from "devextreme/localization";

export default {
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
  },
  components: {
    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling
  },
  data() {
    return {
      datalist: '',
      total: 0,
      pagesize: 20,
      updatalistinfo: false,
      nodeinfoForm: '',
      rules: {
        code: [{
          required: true,
          message: "请输入设备类型编码!"
        }],
        name: [{
          required: true,
          message: "请输入设备类型名称!"
        }]
      },
    };
  },
  created() {

  },
  mounted() {
    this.getSearchDatalist()
  },
  methods: {
    // 初始化数据
    getSearchDatalist() {
      var formData = {
        pageindex: 1,
        pagesize: this.pagesize
      }
      search(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    // 页码数改变
    handleCurrentChange(e) {
      var formData = {
        pageindex: e,
        pagesize: this.pagesize
      }
      search(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    handleSizeChange(e) {
      console.log('当前多少页e :>> ', e);
      this.pagesize = e
    },
    // 数据编辑时打开弹窗
    updatalist(rowInfo) {
      this.nodeinfoForm = rowInfo.data
      // console.log('this.nodeinfoForm :>> ', this.nodeinfoForm);
      this.updatalistinfo = true
    },
    // 编辑、新增后保存信息
    handleSubmit() {

      this.$refs.nodeinfoForm.validate(valid => {
        if (valid) {
          this.updatalistinfo = false
          let formData = new FormData();
          for (let i in this.nodeinfoForm) {
            if (this.nodeinfoForm[i]) {
              formData.append(i, this.nodeinfoForm[i]);
            }
          }
          formData.append('id', this.nodeinfoForm.id);
          searchsave(formData, res => {
            this.getSearchDatalist()
            // this.total = res.count
            // this.datalist = res.data
            // this.datalist.map((x, index) => {
            //   x.key = index + 1;
            // });
          });
          this.getSearchDatalist()
        } else {
          this.updatalistinfo = true
        }
      });

    },
    handleToggleDialog() {
      this.updatalistinfo = false;
    },
    //添加时重置信息
    reset() {
      this.nodeinfoForm = {
        code: "",
        name: "",
        shortname: "",
      };
    },
    //添加
    openDialog() {
      this.reset();
      this.$nextTick(() => {
        this.$refs["nodeinfoForm"].clearValidate();
      });
      this.updatalistinfo = true;
    },
  }
};



