<template>
  <div class="bg">
    <div class="input">
      <el-button @click="openDialog">添加设备类型</el-button>
    </div>
    <div>
      <DxDataGrid
        :data-source="datalist"
        :allow-column-resizing="true"
        :show-column-lines="true"
        :auto-expand-all="false"
        :show-row-lines="true"
        :column-auto-width="true"
        :focused-row-enabled="true"
        :auto-navigate-to-focused-row="true"
        width="98%"
        key-expr="key"
        class="main_table"
        style="height: calc(330px  + 42vh);width:98%"
      >
        <DxColumn data-field="id" alignment="center" caption="ID" />
        <DxColumn data-field="code" alignment="center" caption="设备类型编码" />
        <DxColumn data-field="name" alignment="center" caption="设备类型名称" />
        <DxColumn
          data-field="shortname"
          alignment="center"
          caption="设备类型简称"
        />
        <DxColumn
          data-field="createdate"
          alignment="center"
          caption="创建日期"
        />
        <DxColumn
          :width="210"
          caption="操作"
          alignment="center"
          cell-template="caozuo"
        />
        <template #caozuo="{ data: rowInfo }">
          <div class="caozuo_btn">
            <!-- <span @click="checkCraft(rowInfo)" class="el-icon-view caozuo_edit" title="查看"></span> -->
            <span
              @click="updatalist(rowInfo)"
              class="el-icon-edit caozuo_edit"
              title="修改"
            ></span>
          </div>
        </template>
        <!-- <DxPaging :page-size="pagesize" /> -->
        <!-- <DxPager :visible="true" :allowed-page-sizes="pageSizes" :display-mode="displayMode"
                    :show-page-size-selector="showPageSizeSelector" :show-info="showInfo"
                    :show-navigation-buttons="showNavButtons" /> -->
        <DxScrolling use-native="false" />
      </DxDataGrid>
    </div>
    <el-pagination
      :page-size="pagesize"
      :pager-count="5"
      :page-sizes="[10, 20, 30, 40]"
      @size-change="handleSizeChange"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
    <el-dialog
      title="修改"
      :visible.sync="updatalistinfo"
      width="70%"
      :before-close="handleToggleDialog"
    >
      <div class="dialog_form main_form">
        <el-form
          :rules="rules"
          ref="nodeinfoForm"
          :model="nodeinfoForm"
          label-width="180px"
        >
          <el-row>
            <el-form-item label="设备类型编码" prop="code">
              <el-input
                v-model.trim="nodeinfoForm.code"
                placeholder="请输入设备类型编码"
              >
              </el-input>
            </el-form-item>
          </el-row>
          <el-row>
            <el-form-item label="设备类型名称" prop="name">
              <el-input
                v-model.trim="nodeinfoForm.name"
                placeholder="请输入设备类型名称"
              >
              </el-input>
            </el-form-item>
          </el-row>
          <el-row>
            <el-form-item label="设备类型简称" prop="shortname">
              <el-input
                v-model.trim="nodeinfoForm.shortname"
                placeholder="请输入设备类型简称"
              >
              </el-input>
            </el-form-item>
          </el-row>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="updatalistinfo = false">取 消</el-button>
        <el-button type="primary" @click="handleSubmit('form')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import dtype from "./dtype";
export default dtype;
</script>
<style scoped>
@import url("./dtype.css");
@import url("../../assets/css/houtaicommon.css");
</style>



